html {
  font-size: 100%;
  box-sizing: border-box;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeSpeed;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-primary);
  background-color: var(--blue-900);
}

:root {
  --blue-900: #002547;
  --blue-800: #073464;
  --blue-700: #124982;
  --blue-600: #2d6095;
  --blue-500: #487cb1;
  --blue-400: #6698cc;
  --blue-300: #9dbee1;
  --blue-200: #c6d6e6;
  --blue-100: #dce3e9;
  --yellow-900: #663d00;
  --yellow-800: #995c00;
  --yellow-700: #c97a03;
  --yellow-600: #ee9611;
  --yellow-500: #e8a94a;
  --yellow-400: #eebf77;
  --yellow-300: #ffd494;
  --yellow-200: #ffe8c7;
  --yellow-100: #fff2e0;
}

:root {
  --font-primary: "Proxima Nova", "Trebuchet MS", sans-serif;
}

.grid {
  display: grid;
}

.grid-col__2fr-1fr {
  grid-template-columns: 2fr 1fr;
}
.grid-col__3fr-1fr {
  grid-template-columns: 3fr 1fr;
}

.flex {
  display: flex;
}
.flex-col {
  display: flex;
  flex-direction: column;
}
.flex-inline {
  display: inline-flex;
}

.flow-wrap {
  flex-flow: wrap;
}

.jc-sb {
  justify-content: space-between;
}
.jc-sa {
  justify-content: space-around;
}
.jc-c {
  justify-content: center;
}
.jc-start {
  justify-content: flex-start;
}
.jc-end {
  justify-content: flex-end;
}

.ai-start {
  align-items: flex-start;
}
.ai-end {
  align-items: flex-end;
}
.ai-c {
  align-items: center;
}
.ai-base {
  align-items: baseline;
}

.gap-01 {
  gap: 0.1rem;
}
.gap-02 {
  gap: 0.2rem;
}
.gap-03 {
  gap: 0.3rem;
}
.gap-05 {
  gap: 0.5rem;
}
.gap-06 {
  gap: 0.6rem;
}
.gap-07 {
  gap: 0.7rem;
}
.gap-08 {
  gap: 0.8rem;
}
.gap-09 {
  gap: 0.9rem;
}
.gap-1 {
  gap: 1rem;
}
.gap-15 {
  gap: 1.5rem;
}
.gap-2 {
  gap: 2rem;
}
.gap-3 {
  gap: 3rem;
}
.gap-4 {
  gap: 4rem;
}
.gap-5 {
  gap: 5rem;
}

.w-100 {
  width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  margin: 0;
}

.--bold {
  font-weight: 600;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4375rem;
  padding: 0.4375rem 0.875rem;
  border-radius: 0.3125rem;
  font-weight: 600;
  letter-spacing: 0.00625rem;
}
@media (max-width: 377px) {
  .button {
    font-size: 0.8rem;
    padding: 0.4375rem 0;
    letter-spacing: 0.01875rem;
  }
}
.button--yellow {
  background-color: var(--yellow-400);
  color: var(--blue-900);
  box-shadow: 0 2px 1px var(--yellow-700);
}
.button--yellow:hover, .button--yellow:active {
  background-color: var(--yellow-500);
}
.button--yellow:active {
  transform: translateY(1px);
  box-shadow: none;
}
.button--transparent {
  background-color: transparent;
  color: var(--yellow-400);
  border: 1px solid;
  font-weight: 400;
}
.button--transparent:hover {
  background-color: var(--blue-700);
}
.button--blue {
  background-color: var(--blue-500);
  color: var(--blue-100);
  box-shadow: 0 2px 1px var(--blue-700);
}
.button--blue:hover, .button--blue:active {
  background-color: var(--blue-500);
}
.button--blue:active {
  transform: translateY(1px);
  box-shadow: none;
}

/**-------------------**
| SECTION STYLING
**--------------------**/
.section__heading {
  color: #fff;
  text-align: center;
  letter-spacing: 0.375rem;
  text-transform: uppercase;
  font-weight: 300;
}

/**-------------------**
| ABOUT CARD STYLING
**--------------------**/
.about {
  position: relative;
  z-index: 99;
  padding: 1.875rem;
  max-width: 52ch;
  margin: -3.75rem 1.875rem 0;
  background: linear-gradient(180deg, var(--blue-300) 0%, var(--blue-400) 100%);
  box-shadow: 0px 12px 24px rgba(0, 19, 36, 0.25);
  border-radius: 0.625rem;
}
@media (max-width: 377px) {
  .about {
    margin: -3.75rem 1.875rem 0;
  }
}
.about__heading {
  font-size: 1.125rem;
  color: var(--blue-900);
  margin-bottom: -0.3125rem;
}
.about__subheading {
  font-size: 0.5625rem;
  color: var(--blue-500);
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
}
.about__body {
  font-size: 0.875rem;
  color: var(--blue-900);
  line-height: 1.4;
}
.about__job {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  background-color: var(--blue-100);
  padding: 0.75rem 0.875rem 0.875rem 0.875rem;
  text-align: center;
  border: 1px solid var(--blue-500);
  border-radius: 0.625rem;
}
.about__list-title {
  letter-spacing: 0.0125rem;
  font-weight: 600;
  font-size: 0.875rem;
}
.about__list {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  margin-top: 0;
}
.about__list-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.8125rem;
  list-style-type: none;
  text-align: center;
  line-height: 1.2;
}
@media (max-width: 349px) {
  .about__list-item {
    font-size: 0.75rem;
  }
}
.about__list-item img {
  height: 4rem;
}
@media (max-width: 349px) {
  .about__list-item img {
    height: 2rem;
  }
}

/**-------------------**
| PROJECT CARD STYLING
**--------------------**/
.project__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.project {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2.5rem;
  max-width: 52ch;
  padding: 3.75rem 1.875rem 1.875rem;
  margin: 3.75rem 1.875rem 0;
  color: var(--blue-100);
  border-radius: 0.9375rem;
  background-color: var(--blue-800);
  box-shadow: 0px 12px 24px rgba(0, 19, 36, 0.25);
}
.project__header {
  align-items: center;
  display: flex;
  position: absolute;
  gap: 0.5rem;
  top: -2.8125rem;
  left: 50%;
  width: fit-content;
  height: 45px;
  padding: 0 1rem;
  transform: translateX(-50%);
  background-color: var(--yellow-400);
  border-radius: 0.625rem 0.625rem 0 0;
}
.project .header__logo {
  display: grid;
  place-items: center;
  height: 80px;
  width: 80px;
  background-color: var(--blue-900);
  border: 5px solid var(--yellow-400);
  border-radius: 100%;
}
.project .header__tag {
  color: var(--blue-900);
  font-size: 0.875rem;
  font-weight: 400;
  letter-spacing: 0.01875rem;
}
.project__name {
  display: inline-flex;
  margin-right: 0.3125rem;
  margin-bottom: 0.9375rem;
  font-size: 1.5rem;
}
.project__date {
  display: inline-flex;
  position: relative;
  bottom: 3px;
  font-size: 0.75rem;
  border: 1px solid;
  color: var(--yellow-400);
  padding: 0.1875rem 0.625rem;
  margin-bottom: 0.5rem;
  border-radius: 100vh;
}
.project__body {
  color: var(--blue-200);
  font-weight: 300;
  letter-spacing: 0.01875rem;
  line-height: 1.35;
}
.project__roles {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
.project__skills {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
.project .tag__list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.3125rem;
}
.project .tag {
  border-radius: 100vh;
  font-size: 0.75rem;
  font-weight: 300;
  letter-spacing: 0.0375rem;
  color: var(--blue-300);
  cursor: default;
}
.project .tag__role {
  background-color: var(--blue-700);
  padding: 0.1875rem 0.75rem;
}
.project .tag__role:hover {
  background-color: var(--blue-600);
}
.project .tag__skill {
  background-color: transparent;
  padding: 0.375rem 0.75rem 0.375rem 2rem;
  border: 1px solid var(--blue-700);
}
.project .tag__skill:hover {
  background-color: var(--blue-700);
}
.project__links-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.project__links {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}
.project__links .button {
  width: 100%;
}

/* the first project needs spacing to not hide the heading */
[data-project=vervendo] {
  margin-top: 7.5rem;
}

