.contact {
  padding: 1rem;
  background-image: linear-gradient(180deg, var(--yellow-400) 0%, var(--yellow-300) 100%);
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2.5rem;
  max-width: 52ch;
  padding: 1.875rem;
  margin: 3.75rem 1.875rem 0;
  color: var(--blue-900);
  border-radius: 0.9375rem;
  box-shadow: 0px 12px 24px rgba(0, 19, 36, 0.25);
}
.contact__name {
  font-weight: 600;
  font-size: 1.5rem;
  text-align: center;
}
.contact__body {
  line-height: 1.4;
  text-align: left;
}

.navbar {
  display: flex;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 99999;
  background-color: var(--blue-800);
  padding: 0.9375rem 1.875rem;
  box-shadow: 0 -2px 12px rgba(0, 0, 0, 0.2);
  z-index: 99999;
}
.navbar__logo-container {
  display: inline-flex;
}
@media (max-width: 585px) {
  .navbar__logo {
    max-width: 150px;
  }
}
@media (max-width: 349px) {
  .navbar__logo {
    display: none;
  }
}
.navbar__logo--mobile {
  display: none;
}
@media (max-width: 349px) {
  .navbar__logo--mobile {
    display: block;
  }
}

#canvas {
  position: absolute;
  left: 0;
  height: 800px;
  width: 100%;
}

.hero {
  position: relative;
  background-image: linear-gradient(145deg, var(--blue-900) 45%, var(--blue-800) 75%);
  background-color: var(--blue-900);
  background-attachment: scroll;
  background-repeat: no-repeat;
  background-size: cover;
  height: 35.9375rem;
  padding: 0 1.875rem;
}
.hero__text-container {
  padding-top: 7.5rem;
  text-shadow: 0 2px 2px rgba(0, 0, 0, 0.3);
}
.hero__heading-container {
  margin: 0.375rem 0;
}
.hero__heading {
  margin: 0;
  color: #fff;
  text-transform: uppercase;
  font-size: 2.625rem;
  line-height: 0.9;
  clip-path: inset(0 0 0 0);
  display: inline-flex;
}
.hero .clip-container {
  clip-path: inset(0 0 0 0);
}
.hero__body {
  color: var(--blue-300);
  display: inline-block;
}
.hero__image-container {
  position: absolute;
  z-index: 2;
  right: 0;
  bottom: 0;
  transform-origin: right top;
  transform: scale(1.5) translateY(-5.625rem);
  transition: transform 200ms ease-in;
}
@media (max-width: 585px) {
  .hero__image-container {
    transform: scale(1.3) translateY(-1.875rem);
  }
}
@media (max-width: 377px) {
  .hero__image-container {
    transform: scale(0.8) translateY(0);
  }
}
.hero__image {
  width: 100%;
}

.divider__container {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  padding: 3.75rem 0;
}

.footer__social {
  padding: 5.625rem 0 1.875rem;
}
.footer__credit {
  padding-bottom: 1.875rem;
  font-size: 0.7rem;
  color: var(--blue-200);
  text-align: center;
  font-weight: 300;
  letter-spacing: 0.03125rem;
}
.footer .--yellow-text {
  color: var(--yellow-500);
}